import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    exact: true,
    name: "Home",
    component: Home
  },
  {
    path: "/2017-2018",
    name: "2017-2018",
    component: () => import("../views/2018Page")
    // component: _2018Page
  },
  {
    path: "/2018-2019",
    name: "2018-2019",
    component: () => import("../views/2019Page")
  },
  {
    path: "/2019-2020",
    name: "2019-2020",
    component: () => import("../views/2020Page")
  },
  {
    path: "/articles/:id",
    name: "Articles",
    component: () => import("../views/Article")
  }
];

const router = new VueRouter({
  routes
});

export default router;
