      margin-right: 1em;
      margin-left: 1em;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    border: 5px solid white;
    border-radius: 20px;
    background-color: transparent;
    color: white;
