<template>
  <div>
    <nav :class="`nav ${isOpen ? 'active' : ''}`">
      <router-link
        v-for="link of links"
        :key="link.name"
        :to="link.ref"
        :class="`nav__link ${isOpen ? 'active' : ''}`"
        >{{ link.name }}</router-link
      >
    </nav>

    <div
      @click="isOpen = !isOpen"
      :class="`hamburger ${isOpen ? 'active' : ''}`"
    >
      <span class="hamburger__patty"></span>
      <span class="hamburger__patty"></span>
      <span class="hamburger__patty"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data: () => {
    return {
      isOpen: false,
      links: [
        {
          name: "Home",
          ref: "/"
        },
        {
          name: "Anno 2017/2018",
          ref: "/2017-2018"
        },
        {
          name: "Anno 2018/2019",
          ref: "/2018-2019"
        },
        {
          name: "Anno 2019/2020",
          ref: "/2019-2020"
        }
      ]
    };
  }
};
</script>

<style scoped lang="scss">
@import "../styles/theme";
$navWidth: 350px;
$hamburgerSize: 60px;

* {
  box-sizing: border-box;
}


.nav {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  position: fixed;
  z-index: 3;
  top: 0;
  right: 0;
  width: $navWidth;
  height: 100%;
  background: $primary;
  clip-path: circle(
    29px at calc(#{$navWidth} - 5vw - #{$hamburgerSize / 2})
      calc(5vh + #{$hamburgerSize / 2})
  );
  transition: all 0.8s cubic-bezier(0.86, 0, 0.07, 1);

  &.active {
    clip-path: circle(75% at #{$navWidth / 2} 50vh);
  }

  &__link {
    display: block;
    font-size: 1.5em;
    font-weight: 800;
    text-transform: lowercase;
    letter-spacing: -1px;
    text-align: right;
    padding: 1vh 5vw;
    color: #fff;
    text-decoration: none;
    transition: all 0.8s cubic-bezier(0.86, 0, 0.07, 1);
    opacity: 0;
    transform: translateY(50%);

    @for $i from 1 through 4 {
      &:nth-child(#{$i}) {
        transition-delay: $i * 0.05s;
      }
    }

    &.active {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

.hamburger {
  $p: &;
  width: $hamburgerSize;
  height: $hamburgerSize;
  border-radius: 50%;
  cursor: pointer;
  position: fixed;
  z-index: 10;
  top: 5vh;
  right: 5vw;
  background: $primary;

  &__patty {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 2px;
    width: 50%;
    backgroud-clip: padding-box;
    background: darken(white, 3);
    transition: all 0.8s cubic-bezier(0.86, 0, 0.07, 1);

    &:nth-child(1) {
      transform: translate(-50%, calc(-50% - 8px));
    }

    &:last-child {
      transform: translate(-50%, calc(-50% + 8px));
    }
  }

  &.active {
    #{$p}__patty {
      background: #fff;

      &:nth-child(1) {
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
  }
}
</style>
