import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import VueTyperPlugin from "vue-typer";
import AOS from "aos";
import "aos/dist/aos.css";
import vueScrollTo from "vue-scrollto";

Vue.config.productionTip = false;

Vue.use(VueTyperPlugin);
Vue.use(vueScrollTo, {
  duration: 750
});

new Vue({
  created() {
    AOS.init({
      once: true
    });
  },
  router,
  render: h => h(App)
}).$mount("#app");
